import React, {Component} from 'react';

export default class Blocks extends Component{



    resizeHandle(that,e){
        const element = document.querySelector('.blocksContainer');
        console.log(element,e.clientX);
    }

    render(){

        return(
            <div className="blocks">
                <div className="blocksContainer">
                    <div className="blocksTopLine" onMouseDown={this.resizeHandle.bind(this,event)}/>

                </div>
            </div>
        );
    }
}